export const convertDateToString = (datetime, type = "datetime") => {
  const monthNames = [
    "January",
    "February",
    "March",
    "April",
    "May",
    "June",
    "July",
    "August",
    "September",
    "October",
    "November",
    "December",
  ];
  var date = datetime.toISOString().split("T")[0];
  var day = datetime.getDate();
  var hours = datetime.getHours();
  var year = datetime.getFullYear();
  var minutes = datetime.getMinutes();
  var month = monthNames[datetime.getMonth()];
  var ending = hours >= 12 ? "pm" : "am";
  var displayedhours = hours >= 12 ? hours - 12 : hours;
  if (type == "dateonly") {
    return `${month} ${day}, ${year}`;
  } else {
    return `${date} ${displayedhours}:${minutes} ${ending}`;
  }
};
