import { useState, useEffect } from "react";
import * as FirestoreService from "../services/firestore";
import Signin from "./Signin";
import Signup from "./Signup";
import "../css/auth.css";

const Auth = ({ user }) => {
  const [toggled, setToggled] = useState(false);

  const handleToggle = () => {
    setToggled(!toggled);
  };

  return user ? (
    <div className="auth">
      <h4>You're already logged in</h4>
      <button
        onClick={() => {
          FirestoreService.signout();
        }}
      >
        logout
      </button>
    </div>
  ) : (
    <div className="auth">
      <div
        style={{
          backgroundColor: "var(--tertiary)",
          padding: "1em 0",
          marginBottom: "1em",
        }}
        className="underconstruction"
      >
        <h3>Notice</h3>
        <p>
          currently under construction (admin only) <br /> coming July 2021{" "}
          <br /> for inquiries contact us at pludose@gmail.com
        </p>
      </div>
      {toggled ? (
        <Signup handleToggle={handleToggle} />
      ) : (
        <Signin handleToggle={handleToggle} />
      )}
    </div>
  );
};

export default Auth;
