import { Link } from "react-router-dom";
import "../css/blogcard.css";
import altimg from "../img/plane.jpg";
import { convertDateToString } from "../services/shared";
const handleSelect = (id) => {
  document.getElementById(`cardimg${id}`).classList.add("cardimgselected");
};

const handleUnselect = (id) => {
  document.getElementById(`cardimg${id}`).classList.remove("cardimgselected");
};

const BlogCard = ({ img, title, summary, id, type, created }) => {
  return (
    <Link to={`/blogs/${id}`}>
      <div
        id={`blogcard${id}`}
        className={`blogcard ${type}`}
        onMouseOver={() => handleSelect(id)}
        onMouseOut={() => handleUnselect(id)}
      >
        <img
          id={`cardimg${id}`}
          className="cardimg"
          src={img}
          onError={(e) => {
            e.target.onerror = null;
            e.target.src = altimg;
          }}
          alt=""
        />
        <div className="caption">
          <h1 className="cardtitle">{title}</h1>
          <p className="cardsummary">{summary}</p>
          <p>{convertDateToString(created.toDate(), "dateonly")}</p>
        </div>
      </div>
    </Link>
  );
};

export default BlogCard;
